body {
  background: #f0f2f7;
  justify-content: center;
  margin: 0;
  padding: 40px;
  font-family: Inter, Arial, sans-serif;
  display: flex;
}

.card {
  background: #fff;
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  width: 500px;
  padding: 35px 40px;
  box-shadow: 0 10px 30px #00000014;
}

.card h2 {
  text-align: center;
  margin-bottom: 25px;
}

form {
  width: 100%;
  display: block;
}

label {
  margin-top: 12px;
  font-size: 18px;
  font-weight: 500;
  display: block;
}

.chosen-table {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 600;
}

input, select {
  box-sizing: border-box;
  background: #fafbff;
  border: 1.5px solid #d6d9e0;
  border-radius: 10px;
  margin-top: 6px;
  margin-bottom: 14px;
  padding: 12px 14px;
  font-size: 18px;
  width: 100% !important;
}

.input-wrapper {
  width: 100%;
}

input[type="date"], input[type="time"] {
  appearance: none !important;
  width: 100% !important;
}

input:focus, select:focus {
  background: #fff;
  border-color: #3b82f6;
  outline: none;
  box-shadow: 0 0 0 3px #3b82f633;
}

#no-table-message {
  color: #b30000;
  background: #ffe5e5;
  border: 1px solid #ffb3b3;
  border-radius: 8px;
  margin-top: -5px;
  margin-bottom: 12px;
  padding: 10px;
  display: none;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #c43e4a;
  border: none;
  border-radius: 12px;
  width: 100%;
  padding: 18px;
  font-size: 18px;
  font-weight: 600;
  transition: all .2s;
}

button[type="submit"]:hover {
  background-color: #a83640;
}

.denahImgWrapper img {
  width: 100%;
}
